import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
//COMPONENTS
import { AppLoginComponent } from './components/app-login/app-login.component';
import { RegisterBeneficiarioComponent } from './components/register-beneficiario/register-beneficiario.component';
import { ListBeneficiarioComponent } from './components/list-beneficiario/list-beneficiario.component';
import { UpdateBeneficiarioComponent } from './components/update-beneficiario/update-beneficiario.component';
import { HabilitarBeneficiariosComponent } from './components/habilitar-beneficiarios/habilitar-beneficiarios.component';
import { EmitirBoletaComponent } from './components/emitir-boleta/emitir-boleta.component';
//AUTH
import { AuthGuard } from "./guards/auth.guard";
const routes: Routes = [
  { path: 'register-beneficiario', component: RegisterBeneficiarioComponent, canActivate: [AuthGuard] },
  { path: 'list-beneficiarios', component: ListBeneficiarioComponent , canActivate: [AuthGuard] },
  { path: 'update-beneficiario/:id', component: UpdateBeneficiarioComponent , canActivate: [AuthGuard] },
  { path: 'habilitar-beneficiarios', component: HabilitarBeneficiariosComponent , canActivate: [AuthGuard] },
  { path: 'emitir-boleta', component: EmitirBoletaComponent , canActivate: [AuthGuard] },
  { path: 'login', component: AppLoginComponent },
  { path: '', component: AppLoginComponent },
  // { path: '', component: ListBeneficiarioComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
