import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from "@angular/forms";
import { Router } from '@angular/router';
//services
import { UserService } from "../../services/user.service";
@Component({
  selector: 'app-login',
  templateUrl: 'app-login.component.html',
  styleUrls:['app-login.component.css'],
  providers: [UserService]
})
export class AppLoginComponent implements OnInit {
  loginFrm: FormGroup;
  err: any;
  constructor(
    private fb: FormBuilder,
    private _userService: UserService,
    private router: Router
  ) {
    this.err = {};
    this.buildLoginFrm();
  }
  ngOnInit() {

  }
  buildLoginFrm() {
    this.loginFrm = this.fb.group({
      email: ['', Validators.compose([Validators.required, Validators.email])],
      password: ['', Validators.compose([Validators.required])]
    });
  }
  login() {
    this._userService.login(this.loginFrm.value).subscribe(
      response => {        
        localStorage.setItem('token', response.token);
        localStorage.setItem('user', btoa(JSON.stringify(response.user)));        
        this.router.navigate(['/list-beneficiarios']);                
        console.log("SISTEMA DESARROLLADO POR VICTOR ANAYA");
      }, error => {
        this.loginFrm.reset();
        this.err = error.error;
      }
    )
  }
}
